@import url('https://fonts.googleapis.com/icon?family=Material+Icons');

@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(https://example.com/MaterialIcons-Regular.eot); /* For IE6-8 */
    src: local('Material Icons'),
      local('MaterialIcons-Regular')
  }

  .material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;  /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
  
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;
  
    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;
  
    /* Support for IE. */
    font-feature-settings: 'liga';
  }
  .material-icons.md-48 { font-size: 48px; }
  
body {
  margin: 0;
  background-color: black;
  color: white;
  overflow-x:hidden;
}

h1{
  margin: 0;
}

p{
  margin: 0;
  font-family: 'PT Sans', sans-serif;
}


.cover{
  width: 100%;
  height: 750px;
  object-fit: cover;
  object-position: 10% 50%; 
}

.checked {
  color: orange;
}

.navbar{
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 48px;
  background-color: black;
  justify-content: flex-end;
  box-shadow: 0px 0 5px 1px white;
  z-index: 100;
  top:0 ;
}

.logo{
  margin: 0;
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  padding: 5px;
  position: absolute;
  left: 5px;
  top: 0;
}

#searchsubmit{
  background-color: black;
  color: white;
  border: white 1px solid; /* border prop : COLOR SIZE STYLE */
  font-family: 'PT Sans', sans-serif;
  width: 100px;
  height: 30px;
  font-size: 18px;
  position: absolute;
  right: 50px;
  top: 9px;
}

#searchbar{
  height: 26px;
  border: white 1px solid;
  font-size: 18px;
  top: 9px;
  position: absolute;
  right: 150px;
}

#search{
  align-items: center;
  height: 30px;
  overflow: hidden;
}

#hometitle{
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  padding-top: 60px;
}

#mininavbar{
  position: absolute;
  top: 50px;
  height: 150px;
  width: 200px;
  float: right;
  justify-content: space-evenly;
  flex-direction: column;
  text-align: center;
  background-color: black;
  font-family: 'PT Sans', sans-serif;
  box-shadow: 0px 0px 5px 1px white;
}

.logout{
  background-color: black;
  color: white;
  font-family: 'PT Sans', sans-serif;
  border: 0;
  font-size: 16px;
}


.drinkcard{
  width: 95%;
  height: 200px;
  margin-left: 1.7%;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  background-color: white;
  color: black;
  overflow: scroll;
}

.drinkcontents{
  width: 100%;
  padding-left: 10px;
}

.drinkcontenttop{
  display: flex;
  align-items: baseline;
}

.drinkcontenttop span{
  padding-left: 10px;
}

.drinkcontenttop h1{
  font-family: 'Playfair Display', serif;
}

.drinkcontentbottom p{
  padding-top: 10px;
  padding-bottom: 5px;
}

.drinkcontentstarratings{
  display: flex;
  padding-left: 0;
  padding-top: 5px;
  padding-bottom: 10px;
}

.drinkcontentstarratings span{
  padding: 2px;
}

.drinkcontentmid{
  display: flex;
}

.drinkcontentmid button{
  background-color: black;
  color: white;
  font-family: 'PT Sans', sans-serif;
  width: 100px;
  height: 30px;
  font-size: 18px;
  border: none;
}

.drinkcontentmid input{
  border: black 1px solid;
  padding: 5px;
  font-family: 'PT Sans', sans-serif;
}

#bottomnavbar{
  display: flex;
  flex-direction: column;
  text-align: center;
  font-family: 'PT Sans', sans-serif;
}

#bottomnavbar p:hover{
  font-size: 18px;
}

.ratingcard{
  display: flex;
  width: 95%;
  height: 200px;
  margin-left: 1.7%;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  background-color: white;
  color: black;
  overflow: scroll;
}

.ratingcard button{
  background-color: #000;
  border: none;
  color: #fff;
  font-family: PT Sans,sans-serif;
  font-size: 18px;
  height: 30px;
  width: 100px;
  margin-top: 10px;
  margin-left: 10px;
}

#ratingtitle{
  top: 60px;
  position: relative;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}

#ratingcontainer{
  position: relative;
  top: 60px;
  margin-bottom: 70px;
}

.ratingcontent{
  padding-left: 10px;
}

.ratingcardmid{
  display: 'flex';
  align-items: 'baseline';
  padding-top: 10px;
}

.ratingcardmid span{
  padding-left: 10px;
}

.privacytitle{
  text-align: left;
  padding-left: 20px;
  font-family: 'Montserrat', sans-serif;
}

.privacypolicycontent{
  font-family: PT Sans,sans-serif;
  padding-left: 20px;
  padding-right: 20px;
}

.privacypolicycard {
  margin-bottom: 20px;
  padding-top: 60px;
}

.loginsignupbtn{
  background-color: black;
  color: white;
  text-decoration: 'none';
  width: 100px;
  height: 30px;
  border: white 1px solid;
  font-family: 'PT Sans', sans-serif;
  font-size: 18px;
}

.login{
  display: block;
  position: relative;
  top: 60px;
  width: 600px;
  height: 500px;
  left: 35%;
}

.login h1{
  font-family: 'Montserrat', sans-serif;
}

.login p{
  font-family: PT Sans,sans-serif;
}

.loginsignupinput{
  height: 26px;
  border: white 1px solid;
  font-size: 18px;
  margin-left: 5px;
}

#searchlist{
  display: block;
  position: relative;
  top: 60px;
  padding-bottom: 60px;
}
